@import url("https://fonts.googleapis.com/css2?family=Aboreto&family=Cinzel&family=Gasoek+One&family=Kalam:wght@300&family=Lexend+Deca:wght@600&family=Montserrat:ital,wght@0,400;0,500;0,900;1,900&family=Noto+Sans+Display:wght@500&family=Quicksand&family=Ubuntu&family=Ysabeau+SC&display=swap");

:root {
  --PremiumText: serif;
  --maincolor: #1b7aff;
  --subcolor: #6a6969;
  --lightclr: #d9e6ee5f;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;

  height: 100vh;
  font-family: sans-serif;
  position: relative;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.main {
  margin: 0 auto;
  max-width: 1400px;
  overflow: hidden;
}

/* navigation bar */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 4.2rem;
  background-color: #fff;
  /* box-shadow:1px 1px 6px var(--maincolor); */
  /* border-top: 1px solid var(--maincolor); */
  position: absolute;
  z-index: 10000;
  top: 0;
  position: sticky;
  line-height: 5rem;
}

.first {
  border-radius: 50% 25% 100% 25%;
  z-index: -3;
  position: absolute;
  background-color: #e1a6a6ac;
  opacity: 0.8;
  width: 100px;
  height: 100px;
  top: 35%;
  left: 30%;
}

.second {
  border-radius: 10% 10% 10% 100%;
  z-index: -3;
  position: absolute;
  background-color: #befc7bee;
  opacity: 0.7;
  width: 100px;
  height: 100px;
  top: 10%;
  right: 10%;
}

.third {
  border-radius: 10% 100% 10% 10%;
  z-index: -3;
  position: absolute;
  background-color: #f4d446;
  opacity: 0.8;
  width: 180px;
  height: 180px;
  top: 80%;
  left: 10%;
}

.four {
  border-radius: 100% 20% 40% 10%;
  z-index: -3;
  position: absolute;
  background-color: var(--maincolor);
  opacity: 0.6;
  width: 150px;
  height: 150px;
  top: 45%;
  right: 25%;
}

.menu-bar {
  font-family: sans-serif;
  display: flex;
  gap: 3rem;
}

.logo {
  width: max-content;
  display: flex;
  align-items: center;
}

.logo > img {
  width: 50px;
  height: 50px;
  margin-left: 6rem;
}

.menu-bar > a {
  color: #6a6969;
}

.active {
  color: #000;
  font-weight: 600;
}

li {
  font-size: 1.1rem;
  list-style: none;
  display: flex;
}

.signup {
  font-size: 1.1rem;
  background-color: var(--maincolor);
  color: #fff;
  margin-right: 6rem;
  padding: 0.8rem 2rem;
  border-radius: 2rem;
  font-weight: 600;
  border: none;
}

/* Navigation bar End */

/* Start HOOME PAGE */
.home {
  position: relative;

  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 10rem;
}

.home > h4 {
  margin-top: 5rem;
  text-align: center;
  line-height: 7vh;

  color: rgb(47, 172, 131);
}

.home > h1 {
  font-size: 2.5rem;
  font-weight: 600;
  color: #303030;
  line-height: 1.2;
  text-align: center;
}

.home-img {
  margin: 3rem auto;
  position: relative;
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}

.home-img img {
  max-width: 20rem;
}

/* Collab Section */

.highlight {
  background-color: rgb(99, 185, 228, 0.5);
}

.clb {
  margin-top: 2rem;
  height: max-content;
  background-color: var(--lightclr);
  font-weight: thin;
  color: var(--subcolor);
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Services Secttion */
.services {
  background-color: #cfcfcfbd;
}

.card-container {
  position: relative;
  margin: 5vh 20vh;
  display: grid;
  grid-template-columns: auto auto;
  row-gap: 4rem;
  column-gap: 2rem;
}

.card {
  padding: 2vw 3vw;
  border-radius: 10px;
  line-height: 1.5rem;
  background-color: #f9d8d83d;
  -webkit-background-composite: 0.5;
}

.card > p {
  margin: 10px 0;
}

.card > a > .signup {
  margin: 20px 0;
}

/* Feature Section */
.feature {
  display: grid;
  grid-auto-flow: column;
}

/* footer Section */
.foot {
  padding: 3rem 3rem;
  text-transform: capitalize;
}

.foot-link {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
}

.foot-link > div {
  display: flex;
  flex-direction: column;
  line-height: 2;
  margin-bottom: 2rem;
}

.foot-copyright {
  display: flex;
  justify-content: space-between;
  line-height: 4rem;
}
/* Responsive Sectionn*/

@media screen and (max-width: 1100px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-width: 950px) {
  html {
    font-size: 12px;
  }

  .logo > h3 {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .home-img .img1,
  .img3 {
    display: none;
  }

  .clb {
    line-height: 10rem;
    font-size: 0.6rem;
  }

  .menu-bar {
    display: none;
  }
}

@media screen and (max-width: 650px) {
  html {
    font-size: 12px;
  }
  .foot-copyright {
    line-height: 1;
  }

  .services > h1 {
    line-height: 1;
  }
  .card-container {
    margin: 10% 10%;
  }
}

/* Service section */
@media screen and (max-width: 700px) {
  .card-container {
    display: flex;
    flex-direction: column;
  }
}
